.buttonList{
display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
margin: 15px;
}
.button{
    font-family: -apple-system, BlinkMacSystemFont, 'Josefin Sans', sans-serif;
    font-weight: 500;
    font-size: 16px;
    cursor: pointer;
    
    margin-right: 20px;
    padding: 5px;
    min-width: 70px;
    border-radius: 5px;
    border: 1px solid grey;
     background-color: #fff;
     box-shadow: 0 5px 5px -5px rgba(0, 0, 0, .5);
}